<template>
  <v-bottom-sheet
      persistent
      no-click-animation
      v-model="sheet">
    <v-sheet
        class="text-center"
        height="200px">
      <div class="pt-6">
        
        <a @click="clickTermsOfService"></a>
        
        <a @click="clickPrivacyPolicy"></a>
        
      </div>
      <v-btn
          text
          color="red"
          class="mt-4"
          @click="confirm">
        
      </v-btn>
    </v-sheet>
  </v-bottom-sheet>
</template>

<script>
export default {
  props: {
    sheet: {
      type: Boolean,
      default: false,
    }
  },
  methods: {
    clickTermsOfService() {
      this.$emit("clickTermsOfService")
    },
    clickPrivacyPolicy() {
      this.$emit("clickPrivacyPolicy")
    },
    confirm() {
      this.$emit("confirm")
    },
  },
}
</script>

