<template>
  <v-dialog
      v-model="dialog"
      :width="slideWidth"
      @click:outside="close">
    <v-card>
      <v-card-title></v-card-title>
      <v-divider/>
      <v-carousel
          v-model="slideIndex"
          :width="slideWidth"
          :height="slideHeight">
        <v-carousel-item
            v-for="(slide, i) in slides"
            :width="slideWidth"
            :height="slideHeight"
            :key="i"
            :src="slide">
        </v-carousel-item>
      </v-carousel>
    </v-card>
  </v-dialog>
</template>

<script>
import slide1 from "@/assets/set_block_slide1.png"
import slide2 from "@/assets/set_block_slide2.png"
import slide3 from "@/assets/set_block_slide3.png"


export default {
  props: {
    dialog: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      slideIndex: 0,
      slides: [
        slide1,
        slide2,
        slide3,
      ],
      slideWidth: 864,
      slideHeight: 640,
    }
  },
  methods: {
    close() {
      this.slideIndex = 0
      this.$emit("close")
    },
  },
}
</script>

