<template>
  <v-app :style="{background: colors.grey.lighten3}">
    <v-system-bar
        app
        window
        :style="{background: colors.grey.darken1}"
    >
<!--      <v-btn text x-small @click="btnClickNew">New</v-btn>-->

      <input hidden ref="input" type="file" accept="image/png, image/jpeg" @change="selectedReadFile()"/>
      <v-btn text x-small @click="btnClickRead">Load</v-btn>

      <v-btn text x-small @click="btnClickSave">Save</v-btn>

      <v-btn text x-small @click="btnClickConfig">Config</v-btn>

      <v-btn text x-small @click="btnClickHelp">Help</v-btn>

      <v-spacer/>

      <v-btn icon x-small @click="btnClickToolHelp">
        <v-icon  style="margin-right: 0;">mdi-help-circle</v-icon>
      </v-btn>
    </v-system-bar>

    <v-navigation-drawer
        app
        permanent
        mini-variant
        mini-variant-width="56"
        :style="{background: colors.grey.lighten1}"
    >
      <v-list
          rounded
          dense
          nav
      >
        <v-list-item-group
            v-model="selectedItem"
            mandatory
            color="primary"
        >
          <v-list-item
              v-for="item in items"
              :key="item.icon"
          >
            <v-icon @click="setCursorMode(item.mode)">{{ item.icon }}</v-icon>
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-navigation-drawer>

    <v-navigation-drawer
        app
        right
        permanent
        :style="{background: colors.grey.lighten1}"
    >
      <v-card tile>
        <PreviewCanvas :image-bitmap="$store.state.image.imageBitmap"/>
        <v-card-subtitle>Preview</v-card-subtitle>
      </v-card>
      <v-card tile>
        <v-subheader>: {{ mapPos }}</v-subheader>
        <v-subheader style="height: 24px;">ID: {{ $store.getters.currentBlockId }}</v-subheader>
        <v-subheader class="text-truncate" style="height: 24px;">{{ $store.getters.currentBlockName }}</v-subheader>
        <v-subheader>: {{ $store.getters.currentBrightnessName }}</v-subheader>
      </v-card>

      <template v-slot:append>
        <v-card tile>
          <v-card-subtitle>Original Image</v-card-subtitle>
          <PreviewCanvas :image-bitmap="$store.state.originImage.imageBitmap"/>
        </v-card>
      </template>
    </v-navigation-drawer>

    <v-main>
      <v-container
          class="py-6 px-6"
          fluid
      >
        <v-row>
          <ColorPaletteCanvas/>
        </v-row>
        <v-row ref="drawCanvas">
          <DrawCanvas
              :show-grid="showGrid"
              :show-custom-grid="showCustomGrid"
              :show-block-info="showBlockInfo"/>
        </v-row>
      </v-container>

      <ConvertImageDialog
          :dialog="dialog"
          :image-src="imageSrc"
          @close="closeConvertDialog"/>
      <SaveDialog
          :dialog="saveDialog"
          :image-data="$store.state.image.imageData"
          :image-bitmap="$store.state.image.imageBitmap"
          @close="saveDialog = $event"/>
      <ConfigDialog
          :dialog="configDialog"
          @close="closeConfigDialog"/>
      <PrivacyPolicyDialog
          :dialog="privacyPolicyDialog"
          @close="privacyPolicyDialog = $event"/>
      <TermsOfServiceDialog
          :dialog="termsOfServiceDialog"
          @close="termsOfServiceDialog = $event"/>
      <TutorialDialog
          :dialog="tutorialDialog"
          @close="closeTutorialDialog"/>
      <HowToSetBlockDialog
          :dialog="howToSetBlockDialog"
          @close="howToSetBlockDialog = $event"/>
      <ToolHelpDialog
          :dialog="toolHelpDialog"
          @close="toolHelpDialog = $event"/>
      <AboutDialog
          :dialog="aboutDialog"
          @close="aboutDialog = $event"/>
      <HelpDialog
          :dialog="helpDialog"
          @clickTutorial="tutorialDialog = true"
          @clickHowToSetBlock="howToSetBlockDialog = true"
          @clickTermsOfService="termsOfServiceDialog = true"
          @clickPrivacyPolicy="privacyPolicyDialog = true"
          @clickAbout="aboutDialog = true"
          @close="helpDialog = $event"/>
      <PrecautionBottomSheet
          :sheet="precautionBottomSheet"
          @confirm="confirmPrecaution"
          @clickTermsOfService="termsOfServiceDialog = true"
          @clickPrivacyPolicy="privacyPolicyDialog = true"/>
    </v-main>

    <v-footer
        app
        height="36"
        inset
        padless
        :style="{background: colors.grey.lighten1}">
      <v-row no-gutters align="center">
        <v-btn
            icon
            small
            tile
            class="ma-1"
            @click="normalizeScale">
          <v-icon>mdi-fullscreen</v-icon>
        </v-btn>
        <v-btn-toggle group multiple>
          <v-btn
              v-model="showGrid"
              icon
              small>
            <v-icon>mdi-grid</v-icon>
          </v-btn>
          <v-btn
              v-model="showCustomGrid"
              icon
              small>
            <v-icon>mdi-border-outside</v-icon>
          </v-btn>
          <v-btn
              v-model="showBlockInfo"
              icon
              small>
            <v-icon>mdi-alert-box-outline</v-icon>
          </v-btn>
        </v-btn-toggle>
        <v-spacer/>
        <span class="ma-1">{{ cursorPos }}</span>
      </v-row>
    </v-footer>
  </v-app>
</template>

<script>
import ColorPaletteCanvas from '@/components/ColorPaletteCanvas.vue'
import DrawCanvas from '@/components/DrawCanvas.vue'
import PreviewCanvas from "@/components/PreviewCanvas"
import ConvertImageDialog from "@/components/ConvertImageDialog"
import SaveDialog from "@/components/SaveDialog"
import ConfigDialog from "@/components/ConfigDialog"
import PrivacyPolicyDialog from "@/components/PrivacyPolicyDialog"
import TermsOfServiceDialog from "@/components/TermsOfServiceDialog"
import TutorialDialog from "@/components/TutorialDialog"
import ToolHelpDialog from "@/components/ToolHelpDialog"
import HowToSetBlockDialog from "@/components/HowToSetBlockDialog"
import HelpDialog from "@/components/HelpDialog"
import AboutDialog from "@/components/AboutDialog"
import PrecautionBottomSheet from "@/components/PrecautionBottomSheet"

import colors from 'vuetify/lib/util/colors'

export default {
  components: {
    ConvertImageDialog,
    SaveDialog,
    ConfigDialog,
    PrivacyPolicyDialog,
    TermsOfServiceDialog,
    TutorialDialog,
    ToolHelpDialog,
    HowToSetBlockDialog,
    HelpDialog,
    AboutDialog,
    PrecautionBottomSheet,
    ColorPaletteCanvas,
    DrawCanvas,
    PreviewCanvas,
  },
  data() {
    return {
      selectedItem: 0,
      items: [
        { icon: 'mdi-arrow-all', mode: "move" },
        { icon: 'mdi-pencil', mode: "pencil" },
        { icon: 'mdi-format-color-fill', mode: "fill" },
      ],
      imageSrc: null,
      dialog: false,
      saveDialog: false,
      configDialog: false,
      privacyPolicyDialog: false,
      termsOfServiceDialog: false,
      tutorialDialog: false,
      toolHelpDialog: false,
      howToSetBlockDialog: false,
      helpDialog: false,
      aboutDialog: false,
      precautionBottomSheet: false,
      showGrid: false,
      showCustomGrid: false,
      showBlockInfo: false,
      roughGridSize: 16,
      colors,
    }
  },
  computed: {
    cursorPos() {
      if(this.$store.state.cursorX !== null && this.$store.state.cursorY !== null) {
        const x = this.$store.state.cursorX + 1
        const y = this.$store.state.cursorY + 1
        return `${x}, ${y}`
      } else {
        return ""
      }
    },
    mapPos() {
      if(this.$store.state.cursorX !== null && this.$store.state.cursorY !== null) {
        const x = this.$store.state.config.mapOriginX + this.$store.state.cursorX
        const y = this.$store.state.config.mapOriginZ + this.$store.state.cursorY
        return `${x}, ${y}`
      } else {
        return ""
      }
    },
  },
  methods: {
    // btnClickNew() {
    //   this.$store.commit("setNewImage", {width: 128, height: 128})
    //   this.$store.dispatch("calcImageBitmap")
    // },
    btnClickRead() {
      this.$refs.input.click()
    },
    btnClickSave() {
      if(!this.$store.state.image.imageData) { return }
      this.saveDialog = true
    },
    btnClickConfig() {
      this.configDialog = true
    },
    btnClickHelp() {
      this.helpDialog = true
    },
    btnClickToolHelp() {
      this.toolHelpDialog = true
    },
    selectedReadFile() {
      if(!this.$refs.input.files[0]) { return }

      this.imageSrc = this.$refs.input.files[0]
      this.dialog = true
    },
    setCursorMode(mode) {
      this.$store.commit("setCursorMode", mode)
    },
    closeConvertDialog() {
      this.dialog = false
      this.normalizeScale()

      if(!localStorage.tutorialToSetBlock) {
        this.howToSetBlockDialog = true
        localStorage.tutorialToSetBlock = true
      }
    },
    closeConfigDialog(result) {
      this.configDialog = false
      if(result) { this.$store.commit("setConfig", result) }
    },
    closeTutorialDialog() {
      this.tutorialDialog = false

      if(!localStorage.precautionsConfirmed) {
        this.precautionBottomSheet = true
      }
    },
    confirmPrecaution() {
      this.precautionBottomSheet = false

      localStorage.precautionsConfirmed = new Date().getTime()
      localStorage.tutorial = true
    },
    normalizeScale() {
      const canvasWidth = this.$refs.drawCanvas.clientWidth
      const canvasHeight = this.$refs.drawCanvas.clientHeight
      const imageWidth = this.$store.getters.imageWidth
      const imageHeight = this.$store.getters.imageHeight
      const cellSize = Math.min(canvasWidth / imageWidth, canvasHeight / imageHeight)
      this.$store.commit("setDrawCellSize", cellSize)
      this.$store.commit("setDrawOriginX", (canvasWidth - imageWidth * cellSize) / 2)
      this.$store.commit("setDrawOriginY", (canvasHeight - imageHeight * cellSize) / 2)
    },
  },
  mounted() {
    if(!localStorage.tutorial) {
      this.tutorialDialog = true
    }
  },
}
</script>

